import React from "react";
import axios from "axios";
import { Button, Form } from "react-bootstrap";
import "./forget-password-page1.scss";
import { Link } from "react-router-dom";
import { Toast } from "./../../toaster/toaser";

const ForgetPasswordPage1 = ({ email, setEmail, setId, history: { push } }) => {
  const handleConfirmEmail = (e) => {
    e.preventDefault();
    console.log("here");
    axios
      .post("/forgetPassword", { email })
      .then(({ data: { id } }) => {
        setId(id);
        push("/account/forgetpassword/2");
      })
      .catch((e) => {
        if (e.response?.data)
          Toast(
            "لا يوجد هذا البريد الالكتروني الرجاء التأكد من البريد الالكتروني",
            false
          );
        else Toast("حدث خطأ ما ، الرجاء المحاولة مرة اخرى", false);
      });
  };

  return (
    <Form onSubmit={handleConfirmEmail} className="forget-password-page1-form">
      <Form.Group controlId="formBasicEmail">
        <Form.Control
          className="inputs-style"
          type="email"
          placeholder="البريد الالكتروني"
          required
          value={email}
          onChange={({ target: { value } }) => setEmail(value)}
        />
      </Form.Group>

      <Button
        variant="primary"
        type="submit"
        className="forget-password-page1-button-style"
      >
        التالي
      </Button>
      <div className="forget-password-page1-buttons">
        <Link to="/account/signin">
          <span className="Forget-password-page1-buttons-style">
            تذكرت كلمة المرور ؟ سجل الدخول
          </span>
        </Link>
      </div>
    </Form>
  );
};

export default ForgetPasswordPage1;
